.Splitter {
  display: flex;
}

.Left,
.Right {
  flex: 1;
  /* height: 200px; */
}

.Left {
  text-align: left;
  /* display: flex; */
}

.Right {
  /* background-color: pink; */
  padding: 16px;
}

.ListItem {
  padding: 10px;
}

.Active {
  padding-left: 20px;
  padding-right: 20px;
  background-color: aqua;
  color: black;
  font-weight: bold;
  transition: all 500ms ease;
}

.SliderVertical {
  position: relative;
  background-color: aqua;
  width: 5px;
  transition: all 500ms ease;
}

.SliderHorizontal {
  position: relative;
  background-color: aqua;
  height: 5px;
  transition: all 500ms ease;
  margin: 0px -50px;
}

.Experience {
  padding-top: 50px;
  /* height: 100vh; */
}

@media screen and (max-width: 760px) {
  .Left {
    display: flex;
    overflow-x: auto;
    margin: 0px -50px;
  }
  .Splitter {
    display: block;
  }

  .ListItem {
    /* padding-right: 60px; */
    /* overflow: hidden; */
    white-space: nowrap;
  }
}

@media screen and (max-width: 500px) {
  .Left {
    margin: 0px -25px;
  }
}
